@import url("https://fonts.googleapis.com/css?family=Josefin+Sans");
@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap");

h2 {
  /* font-family: "Josefin Sans", cursive; */
  font-family: "Dancing Script", cursive;
  text-align: center;
  background-color: rgb(110, 184, 147);
  margin-top: 0;
  padding: 10px;
}

.container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-self: center;
}

.setting {
  display: flex;
  flex-direction: column;
  width: 20%;
  justify-content: center;
  align-items: center;
  margin: auto;
}

.memory,
.losing {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 40%;
  justify-content: center;
  box-shadow: 0 0 10px rgb(4, 110, 78);
}

.memory {
  margin: auto;
  margin-bottom: 40px;
}

.time {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
  margin: auto;
  box-shadow: 0 0 10px rgb(4, 110, 78);
  padding: 15px;
}

.players {
  display: flex;
  flex-direction: column;
  width: 20%;
  /* justify-content: center; */
  /* align-items: center; */
  margin: auto;
  box-shadow: 0 0 10px rgb(4, 110, 78);
  padding: 20px;
}

.outPlayers-item {
  box-shadow: 0 0 10px rgb(4, 110, 78);
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 70%;
  margin: auto;
  padding: 30px;
  align-items: center;
}

.toPlayer {
  text-align: center;
}

.getTime {
  font-family: "Dancing Script", cursive;
  font-size: 26px;
  padding-top: 20px;
}

.getTime span {
  font-size: 16px;
  margin-right: 15px;
}

.setTime {
  font-family: "Dancing Script", cursive;
}

.setTime-item input {
  font-family: "Dancing Script", cursive;
  padding: 10px;
  font-size: 20px;
  width: 22%;
  background-color: rgb(110, 184, 147);
  border: none;
}

.setTime-item-p {
  text-align: center;
  font-family: "Dancing Script", cursive;
  font-size: 28px;
  font-weight: bold;
}

.setTime-item-p span {
  font-size: 16px;
  margin-right: 15px;
  font-weight: normal;
}

.cell {
  width: 50%;
  height: 320px;
}

button:hover {
  background-color: rgb(194, 226, 210);
  border: none;
}

.prop {
  width: 20%;
}

.losing,
.finish {
  font-family: "Dancing Script", cursive;
  flex-direction: column;
  align-items: center;
  color: red;
  margin-bottom: 20px;
  width: 30%;
}

.finish {
  color: green;
  text-align: center;
}

.players,
.setTime-item label {
  font-family: "Dancing Script", cursive;
}

.player-1 p:first-of-type,
.player-2 p:first-of-type {
  font-size: 25px;
  margin-bottom: 0;
  font-weight: bold;
}

.points {
  font-size: 26px;
  font-weight: bold;
}

input[type="text"] {
  font-family: "Dancing Script", cursive;
  margin-top: 10px;
  background-color: rgb(110, 184, 147);
  border: none;
  padding: 10px;
  font-size: 20px;
}

.player-1 {
  padding-bottom: 20px;
}

.statistic {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: black;
}

.statistic-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 20px;
  box-shadow: 0 0 10px rgb(4, 110, 78);
}

.statistic-item p {
  padding: 15px;
  height: 30px;
  margin: 0;
}

.number {
  width: 10%;
}

.name {
  width: 70%;
}

.points {
  width: 20%;
}

.form_toggle {
  display: inline-block;
  overflow: hidden;
}
.form_toggle-item {
  float: left;
  display: inline-block;
}
.form_toggle-item input[type="radio"] {
  display: none;
}
.form_toggle-item label {
  display: inline-block;
  padding: 0px 15px;
  line-height: 34px;
  border: 1px solid rgb(4, 110, 78);
  border-right: none;
  cursor: pointer;
  user-select: none;
  padding: 10px 15px;
}

.form_toggle .item-1 label {
  border-radius: 6px 0 0 6px;
}

.form_toggle .item-3 label {
  border-radius: 0 6px 6px 0;
  border-right: 1px solid rgb(4, 110, 78);
}

/* Checked */
.form_toggle .item-1 input[type="radio"]:checked + label,
.form_toggle .item-2 input[type="radio"]:checked + label,
.form_toggle .item-3 input[type="radio"]:checked + label {
  background: rgb(110, 184, 147);
}

.form_toggle .item-1 input[type="radio"]:hover + label,
.form_toggle .item-2 input[type="radio"]:hover + label,
.form_toggle .item-3 input[type="radio"]:hover + label {
  background: rgb(169, 223, 206);
}

.mount-change {
  font-family: "Dancing Script", cursive;
  font-size: 26px;
  margin-bottom: 30px;
}

@media only screen and (min-width: 300px) and (max-width: 992px) {
  .setting {
    width: 90%;
  }

  .losing {
    width: 90%;
  }

  .memory {
    width: 90%;
  }

  .cell {
    height: 100px;
  }

  .container {
    flex-direction: column;
  }

  .time {
    flex-direction: column;
    width: 80%;
    margin-bottom: 20px;
  }

  .players {
    width: 80%;
    margin-bottom: 20px;
  }

  .outPlayers-item {
    flex-direction: row;
    align-items: baseline;
    padding: 0;
    justify-content: space-around;
  }

  input[type="text"] {
    margin-left: 15px;
  }
}
